.info-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: auto;
    overflow: hidden;
}

.chat-container {
    display: flex;
    flex-direction: column;
    height: calc(100vh - 295px);
    width: 100%;
    max-width: 600px;
    margin: auto;
    overflow: hidden;
    position: relative;
}

.chat-box {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}

.fade-out {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background: linear-gradient(to bottom, rgba(240, 240, 240, 1), rgba(255, 255, 255, 0));
    pointer-events: none;
}

.chat-message {
    margin-bottom: 10px;
    padding: 10px 15px;
    border-radius: 5px;
    max-width: 80%;
    word-wrap: break-word;
}

.chat-message.user {
    background-color: #007bff;
    color: white;
    align-self: flex-end;
    text-align: left;
}

.chat-message.bot {
    background-color: #e1e1e1;
    color: black;
    align-self: flex-start;
    text-align: left;
}

.typing-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20px;
}

.typing-indicator span {
    display: inline-block;
    width: 8px;
    height: 8px;
    margin: 0 2px;
    background-color: #000;
    border-radius: 50%;
    animation: bounce 1.2s infinite ease-in-out both;
}

.typing-indicator span:nth-child(1) {
    animation-delay: -0.32s;
}

.typing-indicator span:nth-child(2) {
    animation-delay: -0.16s;
}

.typing-indicator span:nth-child(3) {
    animation-delay: 0s;
}

@keyframes bounce {
    0%, 80%, 100% {
        transform: scale(0);
    }
    40% {
        transform: scale(1);
    }
}

.sample-messages {
    display: flex;
    justify-content: space-around;
    margin: 10px 0;
}

.sample-message-button {
    margin: 0 3px;
    font-size: 11px;
}

.input-container {
    display: flex;
    align-items: center;
    gap: 10px;
}

.input-field {
    flex-grow: 1;
}

.send-button {
    height: 55px;
}

/* Media query for sample messages */
@media (max-width: 500px) {
    .sample-messages {
        flex-wrap: wrap;
        justify-content: center;
    }

    .sample-message-button {
        flex: 1 1 45%; /* Adjust percentage as needed to fit two buttons per row */
        margin: 5px;
    }
}